.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #75184b;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header-logo{
    width: 100px;
    object-fit: contain;
    margin:0 20px;
    margin-top: 10px ;
}
.header-search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
  }

.header-searchIcon{
    padding:5px;
    height: 22px !important;
    background-color: #cd9042;
}
.header-searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header-optionLineOne{
    font-size: 10px;
}

.header-optionLineTwo{
       font-size: 13px;
       font-weight: 800;
}
.header-nav{
    display: flex;
    justify-content: space-evenly;
}

.header-option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}


.header-optionBasket{
    display: flex;
    align-items: center;
    
}
.header-basketCount{
    margin-left: 0px;
    margin-right: 10px;
    color: white;
}
.basket{
    width: 25px;
    object-fit: contain;
    margin:0 20px;
    margin-top: 10px ;
}
