.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    min-width: 700px;
    max-width: 900px;
}
.home-row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    width: auto;

}
.home-image{
    width: 100%;
    z-index: -1;
    margin-bottom:-150px;
    padding: 0px 10px 10px 10px;
    mask-image:linear-gradient(to bottom, rgba(0,0,0,1),rgba(0,0,0,0));
    
}
.home__container{
justify-content: center;
margin-left: auto;
margin-right: auto;
max-width: 1100px;
}

